const crypto = require('@trust/webcrypto');
const keyto = require('@trust/keyto')
const {JWD} = require('@trust/jose')

const privateHex = '57eba9c003d930e8233f502d8f0cc552e7ee75942b273a41c26d3bbb97b924e2';
const key = keyto.from(privateHex, 'blk');
const privateJwk = key.toJwk('private');
const publicJwk = key.toJwk('public');


class Crypto {

    constructor() {

        Promise.all([
                crypto.subtle.importKey('jwk', privateJwk, {
                    name: 'ECDSA',
                    namedCurve: 'K-256',
                    hash: {name: 'SHA-256'}
                }, true, ['sign']),
                crypto.subtle.importKey('jwk', publicJwk, {
                    name: 'ECDSA',
                    namedCurve: 'K-256',
                    hash: {name: 'SHA-256'}
                }, true, ['verify']),
            ])

            // use key with JWA to create a signature
            .then(keypair => {
                console.log('!!!!!')

            });

        this.sign = this.sign.bind(this)


    }

    sign(item) {
        console.log('signing....', item);
       // let doc = JWD(item)

        //doc.sign({
        //    signatures: [
        //        {
        //            protected: {alg: 'KS256'},
        //            cryptoKey: keypair[0]
        //        }
        //    ]
        //})

    }


}

export default new Crypto();


